body {
  margin: 0;
  padding: 0;
    
  max-width: 100%;
    
  background: -webkit-gradient(linear, left top, left bottom, from(#323051), to(#212938));
    
  background: -webkit-linear-gradient(#323051, #212938);
    
  background: -o-linear-gradient(#323051, #212938);
    
  background: linear-gradient(#323051, #212938);
      
  color: white;
    
  font-family: "Titillium Web", sans-serif;
}

#root {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    
    width: 100%;
    max-width: 600px;
    margin: auto;
    
    min-height: 100vh;
}

button {
    font-family: "Titillium Web", sans-serif;
}


