#choice-div {
    display: -ms-flexbox;
    display: flex;
    
    -ms-flex-direction: column;
    
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    
    margin-top: 40px;
}

.choice-btn {
    display: block;
    margin: 20px auto;
    
    height: 60px;
    width: 120px;
    
    padding: 0px;
    
    /*
    * These larger values would be used with real story options.
    * They should fit most text, but feel free to adjust them as needed.
    * height: 250px;
    * width: 250px;  
    * padding: 20px;
    */
    
    background: transparent;
    
    color: white;
    
    font-size: 20px;
}

@media (min-width: 620px) {
    #choice-div {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-areas: 
            
            "Option1 Option2"
            "Option3 Option3";
        
        grid-gap: 40px;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        
        margin: auto;
        
        padding-top: 40px;
    }
    
    [value="option1"] {
        grid-area: Option1;
    }
    
    [value="option2"] {
        grid-area: Option2;
    }
    
    [value="option3"] {
        grid-area: Option3;
    }
    
    .choice-btn {
        margin: auto;
    }
}

@media (min-width: 960px) {
    #choice-div {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-pack: center;
            justify-content: center;
    
        margin-top: 40px;
    }
    
    .choice-btn {
        margin: auto 20px;
    }
}





